// App
import React from "react";

// Article
import Article from "./Article";
import Pagination from "../Elements/Pagination";
import ArticleHeader from "./Partials/ArticleHeader";
import configApi from "../config/api";
import { withCookies } from "react-cookie";
import Loading from "../Elements/Loading";
import { CSSTransitionGroup } from "react-transition-group";
class Articles extends React.Component {
  state = {
    articles: [],
    currentPage: 0,
    pageCount: false,
    isFething: true,
  };
  componentDidMount() {
    this.getAricles(0);
  }
  getAricles(page = 0) {
    this.setState({ isFething: true }, () => {
      fetch(`${configApi.urlApi}/api/article/getArticles`, {
        method: "post",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          filter: this.props.filter,
          page: page,
        }),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data.articles) {
            this.setState({
              pageCount: data.pageAll,
              currentPage: page,
              articles: data.articles,
              isFething: false,
            });
          }
        });
    });
  }
  setPage(selected) {
    if (selected !== this.state.currentPage) {
      this.getAricles(selected);
    }
  }
  render() {
    return (
      <div className="articles-block">
        <ArticleHeader></ArticleHeader>
        <Loading isLoading={this.state.isFething}></Loading>
        <CSSTransitionGroup
          transitionName="height-animation-item"
          transitionEnterTimeout={500}
          transitionLeaveTimeout={1}
          style={{
            display: "contents",
          }}
        >
          {this.state.articles.map((article, i) => {
            return (
              <Article
                IsManage={this.props.IsManage}
                key={i}
                article={article}
              />
            );
          })}
        </CSSTransitionGroup>
        {!this.state.articles.length && (
          <div className="text-center py-3">ÐÐ°Ð¿Ð¸ÑÐµÐ¹ Ð½Ðµ Ð½Ð°Ð¹Ð´ÐµÐ½Ð¾</div>
        )}
        {!!this.state.articles.length && (
          <Pagination
            currentPage={this.state.currentPage}
            pageCount={this.state.pageCount - 1}
            onPageChange={this.setPage.bind(this)}
          />
        )}
      </div>
    );
  }
}

export default withCookies(Articles);
